<template>
   <div class="page_container">
        <el-form :model="searchForm" ref="searchForm" label-width="100px" size="small">
            <div class="search-row">
                <div>
                    <el-form-item label="" prop="name">
                        <el-input v-model="searchForm.name" placeholder="" :maxlength="30"></el-input>
                    </el-form-item>
                </div>
                <div>
                    <el-form-item label="" prop="idCard">
                        <el-input v-model="searchForm.idCard" placeholder="" :maxlength="20"></el-input>
                    </el-form-item>
                </div>
                <div>
                    <el-form-item label="" prop="patientPhone">
                        <el-input v-model="searchForm.patientPhone" placeholder="" :maxlength="15"></el-input>
                    </el-form-item>
                </div>
                <div>
                    <el-form-item label="" prop="cardNo">
                        <el-input v-model="searchForm.cardNo" placeholder="" :maxlength="20"></el-input>
                    </el-form-item>
                </div>
                <div class="btns">
                    <el-form-item label-width="20px">
                        <el-button @click="resetForm" class="fl"></el-button>
                        <el-button type="primary" @click="formSearch" class="fl"></el-button>
                        <el-button type="primary" class="fr" @click="toPage">vue</el-button>
                    </el-form-item>
                </div>
            </div>
        </el-form>
    <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" size="small"
        :header-cell-style="{
                    background:'#EBF4FF',
                    color: '#333',
                    fontWeight: 'bold',
                    height: '40px',
                    padding: 0,
                    fontSize: '14px'
                }" class="search-table" height="500" 
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
        element-loading-background="hsla(0,0%,100%,.9)" :cell-style="{color: '#333', fontSize: '14px'}" height="calc(100% - 100px)">
        <el-table-column type="selection" width="55">
        </el-table-column>
        <el-table-column type="index" width="50" label="">
        </el-table-column>
        <el-table-column prop="mpiId" label="" show-overflow-tooltip min-width="220">
        </el-table-column>
        <el-table-column prop="healthInsuranceCardId" label="" show-overflow-tooltip min-width="100">
        </el-table-column>
        <el-table-column prop="name" label=""  min-width="70" show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="sexName" label="" min-width="50">
            <template slot-scope="scope">
                <span>{{scope.row.sexName || scope.row.sex }}</span>
            </template>
        </el-table-column>
        <el-table-column prop="birthDate" label="" min-width="90">
            <template slot-scope="scope">
                <span>{{scope.row.birthDate && scope.row.birthDate.slice(0, 10)}}</span>
            </template>
        </el-table-column>
        <el-table-column prop="idCard" label="" show-overflow-tooltip min-width="120">
        </el-table-column>
        <el-table-column prop="patientPhone" label="" min-width="100">
        </el-table-column>
        <el-table-column label="" class-name="action">
            <template slot-scope="scope">
                <el-tooltip effect="dark" content="" placement="bottom">
                    <span class="operate-icon operate-icon1 hand co_blue">
                        <i class="iconfont icon-ic_more"></i>
                    </span>
                </el-tooltip>
            </template>
        </el-table-column>
    </el-table>
     <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNo"
        :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total" class="flex_end p16">
    </el-pagination>
   </div>
   
</template>
<script>
import { queryListData } from '../../api/service';
import { statusEnum } from '../../enums/enums.m.js';
export default {
    components: {
    },
    data() {
        return {
            searchForm: {
                name: '',
                idCard: '',
                patientPhone: '',
                cardNo: ''
            },
            pageNo: 1,
            pageSize: 10,
            currentPage: 1,
            total: '',
            tableData: [],
        }
    },
    methods: {
        formSearch() {
            this.pageNo = 1;
            this.queryMainIndexList();
        },
        handleSizeChange(val) {
            this.pageSize = val;
            this.pageNo = 1;
            this.queryMainIndexList();
        },
        handleCurrentChange(val) {
            this.pageNo = val;
            this.queryMainIndexList();
        },
        resetForm() {
            this.$refs.searchForm.resetFields();
            this.pageNo = 1;
            this.pageSize = 10;
            this.queryMainIndexList();
        },
        queryMainIndexList() {
            let me = this;
            me.tableLoading = true;
            let params = {
                pageNum: me.pageNo,
                pageSize: me.pageSize,
                isTc: false
            }
            Object.assign(params, me.searchForm)
            $ajax({
                url: 'api/mpiview.mpiManageRpcService/selectPatientInfoByParamsForPage',
                jsonData: [params]
            }).then(function (res) {
                if (res && res.code == 200) {
                   me.tableData = res.body.list;
                   me.pageSize = res.body.pageSize;
                   me.total = res.body.total;
                   me.pageNo = res.body.pageNum;
                }
                me.tableLoading = false;
            }).fail(function (e) {
                console.error(e);
                me.tableLoading = false;
            })
        },
        toPage() {
            let a = new ssdev.ux.portal.SubPortal();
            a.$portal = $portal;
            a.onOpen('app1', 'vueComp1')
            console.log(a)
        },
        requestTest() {
            let params = {
                pageNum: this.pageNo,
                pageSize: this.pageSize,
                isTc: false
            }
            Object.assign(params, this.searchForm)
            queryListData([params]).then((res) => {
              if (res && res.code == 200) {
                console.log(res)
              }
            });
        }
    },
    created() {
        this.queryMainIndexList()
        this.requestTest()
        console.log(1111)
    }
}
</script>
